import { Router } from "@angular/router";
import { FormBuilder } from '@angular/forms';
import { Component, OnInit, OnDestroy } from '@angular/core';
import {
  ControlAnchor,
  MapOptions,
  NavigationControlOptions,
  NavigationControlType,
  Point,
  BMarker,
  BMapInstance,
} from 'angular2-baidu-map';
import { UserService } from 'src/app/shared/Service/userService/user.service';
import { ToasterService } from 'src/app/shared/Module/toaster/toaster.service';
@Component({
  selector: 'app-monitor',
  templateUrl: './monitor.component.html',
  styleUrls: ['./monitor.component.css']
})
export class MonitorComponent implements OnInit, OnDestroy {
  //user
  user;
  interval;
  //Map_Settings
  markers = [];
  title = 'angular5 Baidu-Map example';
  optionsMap: MapOptions;
  mapControl: BMapInstance;
  point: Point;
  navOptionsMap: NavigationControlOptions;
  mapcenter = {
    lat: 39.915,
    lng: 116.404,
  };
  sight = 400;
  mapzoom = 17;
  //SearchBar_Settings
  key;
  keyTable;
  srcCol=['license_plate_number','camera_id','lat','lng','track_time','status','brand','color'];
  //selectionTable_Settings.
  foundedItem = [];
  selectionStatus = [];
  selectAll = true;
  statusOfVehicle = { start: 'fas fa fa-car float-right start', stop: 'fas fa fa-car float-right stop', unknown: 'fas fa fa-eye-slash float-right stop'}
  //forMouseMoveIntroduce
  showIntroduction = false;
  showPosition = { x: "100px", y: "100px" };
  //for table vehicle
  tableStatus = false;
  vehicleList = [];
  usertableFilter1;
  tableVal = [];
  constructor(
    private userService: UserService,
    private router: Router,
    private _formBuilder: FormBuilder,
    private toastr: ToasterService,
  ) { }

  ngOnInit(): void {
    //user
    const role = this.userService.getToken()['role'];
    const user = this.userService.getToken()['userInfo'];
    if (role == 'agent') this.user = { phone: user.contact_number, role: role }
    else if (role == 'admin') this.user = { phone: user.phone_number, role: role }
    else this.user = { phone: user.phone, role: role }
    //map
    this.optionsMap = {
      centerAndZoom: {
        lat: this.mapcenter.lat,
        lng: this.mapcenter.lng,
        zoom: this.mapzoom,
      },
      enableKeyboard: true,
      enableScrollWheelZoom: true,
    };
    this.navOptionsMap = {
      anchor: ControlAnchor.BMAP_ANCHOR_TOP_RIGHT,
      type: NavigationControlType.BMAP_NAVIGATION_CONTROL_ZOOM,
    };
  }
  ngOnDestroy(){
    clearInterval(this.interval)
  }
  //get Vehicle list and current position
  getAll() {
   const innn = () => {
     this.userService.postRequest('api/getVehicleInfo', this.user).subscribe(
      res => {
        this.setAll(res['result']);
      },
      err => {
        this.handleError(err)
      }
    );
   }
   innn();
   this.interval = setInterval(innn,5000);
  }
  async setAll(data) {
    const tempItem = [];
    const tempStatus = [];
    for (let i = 0; i < data.length; i++) {
      let d = data[i];
      tempStatus.push(true);
      const status = await this.getStatus(d);
      const position = await this.getPosition(d);
      if(position.length>0){
        d.camera_id=position[0].camera_id;
        d.lat=position[0].lat;
        d.lng=position[0].lng;
        d.track_time=position[0].track_time;
      }else{
        d.camera_id='not found';
        d.lat= 0;
        d.lng= 0;
        d.track_time='';
      }
      d.status = status;
      tempItem.push(d);
    }
    this.foundedItem = tempItem;
    this.selectionStatus = tempStatus;
    this.vehicleList= tempItem;
    this.setMap(tempItem);
  }
  //get vehicle status
  async getStatus(vehicle) {
    try {
      const status = await this.userService.postRequest('api/getVehicleStatus', vehicle).toPromise();
      return status['result']
    } catch (error) {
      return 'unknown'
    }
  }
  //get vehicle status
  async getPosition(vehicle) {
    try {
      const status = await this.userService.postRequest('api/getPosition', vehicle).toPromise();
      return status['result']
    } catch (error) {
      return []
    }
  }  
  //for VehicleInfo
  showInfo() {
    this.tableStatus = true;
  }
  tableClose() {
    this.tableStatus = false;
  }
  //For selection table
  checkBoxall() {
    this.checkSetAll(this.selectAll);
  }
  checkSetAll(status) {
    for (let i = 0; i < this.selectionStatus.length; i++) {
      this.selectionStatus[i] = status;
    }
  }
  checkKey(key, string: string) {
    if (!key) return true;
    if (string.indexOf(key) != -1) return true
    else return false;
  }
  applyFilter(){
    const temp=[];
    if(!this.keyTable) {
      this.vehicleList = this.foundedItem;
      return;
    }
    for(let i=0 ; i< this.foundedItem.length; i++){
      if(this.checkTableSearch(this.foundedItem[i])) temp.push(this.foundedItem[i])
    }
    this.vehicleList=temp;
  }
  checkTableSearch(arr){//search function of table
    if(!this.keyTable) return true;
    for(let i=0;i<this.srcCol.length;i++){
      if(String(arr[this.srcCol[i]]).search(this.keyTable)!=-1){
        return true;
      }
    }
    return false;
  }
  //toastr functions
  handleError(err) {
    if (err.status == 504) this.Toaster('error', '', 'server is not responsing.', 4000);
    else this.Toaster('error', '', err.error.message, 1500);
  }
  Toaster(style, msgtopic, msgContent, time = 2000) {
    this.toastr.show(style, msgtopic, msgContent, time);
  }
  //For Maps
  mapLoaded(e: BMapInstance) {
    this.mapControl = e;
    this.getAll();
  }
  setMap(opt) {
    if (opt.length == 0) return;
    this.setZoomAndCenter(opt);
    for (let i = 0; i < opt.length; i++) {
      const newMarker = {
        canvaslayerOptions: this.getCanvasLayer(opt[i]),
      };
      this.markers.push(newMarker);
    }
  }
  getCanvasLayer(opt) {
    const layer = {
      update(map: BMapInstance, canvas: HTMLCanvasElement) {
        const ctx = canvas.getContext('2d')
        if (!ctx) {
          return
        }
        ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height)
        var data = new window.BMap.Point(opt.lng, opt.lat)
        var pixel = map.pointToPixel(data);
        //set
        const radius = 30;
        const textsize = 15;
        const circleLineWidth = 5;
        const textLineWidth = 0.5;
        const name: string = opt.license_plate_number;
        const text = name.substr(0, Math.floor(1.5 * radius / textsize));
        //circle
        ctx.beginPath();
        ctx.save();
        ctx.fillStyle = "rgba(34, 25, 25, 0.63)";
        ctx.arc(pixel.x, pixel.y, radius, 0, 2 * Math.PI);
        ctx.strokeStyle = 'red';
        ctx.lineWidth = circleLineWidth;
        ctx.fill();
        ctx.stroke();
        ctx.restore();
        //text
        ctx.beginPath();
        ctx.save();
        ctx.textAlign = "center";
        ctx.textBaseline = "middle";
        ctx.strokeStyle = 'white';
        ctx.lineWidth = textLineWidth;
        ctx.font = "" + (textsize) + "px Arial";
        // ctx.strokeText(text, pixel.x, pixel.y);
        ctx.stroke();
        ctx.font = "" + textsize + "px Arial";
        ctx.fillStyle = "white";
        ctx.fillText(text, pixel.x, pixel.y);
        ctx.fill();
        ctx.restore();
      }
    }
    return layer;
  }
  setZoomAndCenter(opt) {
    const CZ = this.getCenter(opt);
    const center = {
      lat: CZ.lat,
      lng: CZ.lng,
      equals: Boolean,
    };
    const centerCritical = {
      lat: this.mapControl.getCenter().lat,
      lng: this.mapControl.getCenter().lng,
      equals: Boolean,
    }
    const x = (this.mapControl.pointToPixel(centerCritical).x - this.mapControl.pointToPixel(center).x);
    const y = (this.mapControl.pointToPixel(centerCritical).y - this.mapControl.pointToPixel(center).y);
    const zoom = CZ.zoom;
    this.mapControl.panBy(x, y, { noAnimation: false });
    this.mapControl.setZoom(zoom);
    this.mapcenter = { lat: CZ.lat, lng: CZ.lng };
  }
  getCenter(opt) {
    if (opt.length == 0) return;
    if (opt.length == 1) return { lat: opt[0].lat, lng: opt[0].lng, zoom: this.mapControl.getZoom() };
    let minLat, maxLat, minLng, maxLng, avgLat, avgLng;
    minLat = Number(opt[0].lat);
    maxLat = Number(opt[0].lat);
    minLng = Number(opt[0].lng);
    maxLng = Number(opt[0].lng);
    for (let i = 0; i < opt.length; i++) {
      if (opt[i].lat < minLat || minLat== 0) minLat = Number(opt[i].lat);
      if (opt[i].lat > maxLat) maxLat = Number(opt[i].lat);
      if (opt[i].lng < minLng || minLng== 0) minLng = Number(opt[i].lng);
      if (opt[i].lng > maxLng) maxLng = Number(opt[i].lng);
    }
    avgLat = minLat + (maxLat - minLat) / 2;
    avgLng = minLng + (maxLng - minLng) / 2;
    const min = {
      lat: minLat,
      lng: minLng,
      equals: Boolean,
    };
    const max = {
      lat: maxLat,
      lng: maxLng,
      equals: Boolean,
    }
    const width = Math.abs(this.mapControl.pointToPixel(min).x - this.mapControl.pointToPixel(max).x);
    const height = Math.abs(this.mapControl.pointToPixel(min).y - this.mapControl.pointToPixel(max).y);
    const critera = Math.max(width, height);
    const zoom = this.mapControl.getZoom() - Math.log2(critera / this.sight);
    return { lat: avgLat, lng: avgLng, zoom: zoom };
  }
  setAnimation(marker: BMarker): void {
    // marker.setAnimation(Animation.BMAP_ANIMATION_BOUNCE);
  }
  showWindow({
    marker,
    map
  }: {
    marker: BMarker
    map: BMapInstance
  }): void {
    map.openInfoWindow(
      new window.BMap.InfoWindow('地址：浦东南路360号', {
        offset: new window.BMap.Size(0, -30),
        title: '新上海国际大厦'
      }),
      marker.getPosition()
    );
  }
}
